<template>
    <div>
        <div class="sign_body">
            <form class="form-signin text-center">
                <img src="../../assets/vue shop logo-03.png" width="150px" height="150px" alt="img" />
                <h1 class="h3 mb-3 font-weight-normal"></h1>
                <div class="emailenter_box text-left mt-3">
                    <label for="inputEmail" class=""></label>
                    <input
                        type="email"
                        id="inputEmail"
                        class="form-control rounded-pill"
                        placeholder=""
                        required
                        autofocus
                    />
                </div>
                <div class="password_box text-left">
                    <label for="inputPassword" class="mt-3"></label>
                    <input
                        type="password"
                        id="inputPassword"
                        class="form-control rounded-pill"
                        placeholder=""
                        required
                    />
                </div>
                <button class="btn btn-primary btn-block my-5 sign_btn w-50 mx-auto" type="submit"></button>
                <router-link to="/backstage_sign" class="btn text-black-50 border-bottom p-0"></router-link>
            </form>
        </div>
    </div>
</template>

<script>
export default {
    name: "HelloWorld",
    data() {
        return {
            msg: "Welcome to Your Vue.js App"
        };
    }
};
</script>



